export default {
	data() {
		return {
			showMask: false, //显示遮罩层
			showPopup: false, //显示弹出层
		}
	},
	methods: {
		//显示
		show() {
			this.showMask = true
			this.showPopup = true
		},
		//隐藏
		hide() {
			this.showPopup = false
			setTimeout(() => {
				this.showMask = false
			}, 800)
		}
		
	}
}
